@import 'base/config';
@import 'layout/home';
@import 'layout/menu';
@import 'layout/about';
@import 'layout/projects';
@import 'layout/contact';
@import 'layout/responsive';

body {
    background: $primary-color;
    color:set-text-color($primary-color);
    height: 100vh;
    font-family: $fuentePrincipa;
    line-height: 1;
}

h1, h2, h3 {
    font-weight: 400;
}

a {
    color: set-text-color($primary-color);
    text-decoration: none;
}

.text-secondary {
    color: $secondary-color;
}

header {
    position: fixed;
    z-index: 2;
    width: 100%;
    padding: 1rem;
}

main {
    height: 100%;
    width: 100%;

    .social-icons {
        position: fixed;
        bottom: 1rem;
        left: 1rem;

        a {
            padding: 0.4rem;
            &:hover {
                color: $secondary-color;
                @include transition-ease; 
            }
        }
    }
}

footer {
    font-size: 1rem;
    position: fixed;
    bottom: 0.4rem;
    right: 1rem;
    text-align: right;
    padding: 1rem;
    color: set-text-color($primary-color);
}