.projects {
    padding-bottom: 2rem;

    &__bio-image {
        height: 50vh;
        width: 100%;
        background:
            linear-gradient(to right,
                rgba($primary-color, 0.9),
                rgba($primary-color, 0.3),

            ),
            url(/build/img/yo.jpg);

        background-size: cover;
        background-position: top center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        
        .text-secondary {
            font-size: 2rem;
            margin-bottom: 2rem;
        }
    }
   
    &__items {
        width: 60vw;
        margin: 2rem auto;
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
        
    }
    &__item {
        position: relative;
        border-bottom: 5px solid $secondary-color;
        overflow: hidden;
        cursor: pointer;
        
        img {
            width: 100%;
        }

        &::after {
            content: '';
            position: absolute;
            top: 100%;
            left: 0;
            height: 100%;
            width: 100%;
            background: $secondary-color;
            opacity: 0;
            @include transition-ease
        }
        &:hover {
            &::after {
                top: 0;
                opacity: .9;
            }
            .projects__btn {
                opacity: 1;
            }
            
            
        }
       
    }
    &__btns {
        position: absolute;
        top: 0;
        height: 100%;
        width: 100%;
        z-index: 1;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
        text-align: center;
    }
    &__btn {
        opacity: 0;
        color: set-text-color($secondary-color);
        @include transition-ease;

        &:hover {
            color: set-text-color(set-text-color($secondary-color));
            
        }
    }




    
    .social-icons {
        display: flex;
        flex-direction: column;

    }
    footer {
        transform: rotate(90deg) translate(-3.5rem, -4rem);
    }
}