.contact {
    height: 100%;
    padding-top: 40vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: 
        linear-gradient(
            to right,
            rgba($primary-color, 0.9),
            rgba($primary-color, 0.3),

        ),
    url(/build/img/yo.jpg);
        
    background-size: cover;
  

    h2 {
        font-size: 3rem;
        margin-bottom: 2rem;
    }
    &__list {
        margin: 0 20rem;
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
        font-size: 1.5rem; 
        .text-secondary {
            margin-top: .3rem;
        }
    }
    &__address {
        

        .text-secondary {
            font-size: 1rem;
            font-weight: 100;
        }
    }
}
.social-icons {
    position: initial !important;
    margin-top: 2rem;
}