.about {
    padding-bottom: 2rem;

    &__bio-image {
        height: 50vh;
        width: 100%;
        background:
            linear-gradient(to right,
                rgba($primary-color, 0.9),
                rgba($primary-color, 0.3),

            ),
            url(/build/img/yo.jpg);

        background-size: cover;
       background-position: top center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        p {
            margin-bottom: 2rem;
        }
    }
    &__bio {
        width: 80%;
        text-align: center;
        .text-secondary {
            padding-bottom: 1rem;

        }
    }
    .jobs {
        width: 60vw;
        margin: 2rem auto 0;
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
        &__job {
            background: lighten($primary-color, 10%);
            padding: .5rem;
            border-bottom: 5px solid $secondary-color;
            h2, h3 {
                margin: 0.5rem 0;
            }
            h6 {
                margin: .3rem 0;
            }
        }
    }
    .social-icons {
        display: flex;
        flex-direction: column;

    }
    footer {
        transform: rotate(90deg) translate(-3.5rem, -4rem);
    }
}